export default function MediaCard({ media }) {
  return (
    <div className="rounded-2xl bg-white shadow-md hover:shadow-xl transition border border-gray-100 overflow-hidden center">
      <div className="p-3">
        {media.type === "video" ? (
          <video
            controls
            className="w-full rounded-lg max-h-[300px] object-cover"
          >
            <source src={media.url} />
          </video>
        ) : (
          <audio controls className="w-full">
            <source src={media.url} />
          </audio>
        )}
      </div>

      <div className="px-4 pb-4 text-sm text-gray-500">
        {media.type === "video" ? "ðŸŽ¥ Video" : "ðŸŽ§ Audio"}
      </div>
    </div>
  );
}
